          this.$notify({
            type: 'error',
            title: '获取排行榜数据失败',
            message: res.msg
          })
        } else {
          this.attendance = res.data
          this.createChart()
        }
      }).catch(err => {
        this.$notify({
          type: 'error',
          title: '获取排行榜数据失败',
          message: err
        })
      })
    },
