                let ind = index + 1
                if (ind === countNames.length) {
                  return "{one|" + (countNames.length - index) + "} {a|" + value + "}"
                } else if (ind + 1 === countNames.length) {
                  return "{two|" + (countNames.length - index) + "} {b|" + value + "}"
                } else if (ind + 2 === countNames.length) {
                  return (
                      "{three|" + (countNames.length - index) + "} {c|" + value + "}"
                  )
                }
                if (countNames.length - index > 9) {
                  return (
                      "{five|" + (countNames.length - index) + "} {d|" + value + "}"
                  )
                }
                return "{four|" + (countNames.length - index) + "} {d|" + value + "}"
              },
              rich: {
                a: {
                  color: "dodgerblue",
                  fontWeight: 'bolder',
                },
                b: {
                  color: "dodgerblue",
                  fontWeight: 'bolder',
                },
                c: {
                  color: "dodgerblue",
                  fontWeight: 'bolder',
                },
                d: {
                  color: "dodgerblue",
                  fontWeight: 'bolder',
                },
                // 第一名
                one: {
                  backgroundColor: "#E86452",
                  color: "white",
                  width: 12,
                  height: 16,
                  padding: [1, 0, 0, 5],
                  borderRadius: 10,
                  fontSize: 11,
                },
                // 第二名
                two: {
                  backgroundColor: "#FF9845",
                  color: "white",
                  width: 12,
                  height: 16,
                  padding: [1, 0, 0, 5],
                  borderRadius: 10,
                  fontSize: 11,
                },
                // 第三名
                three: {
                  backgroundColor: "#F6BD16",
                  color: "white",
                  width: 12,
                  height: 16,
                  padding: [1, 0, 0, 5],
                  borderRadius: 10,
                  fontSize: 11,
                },
                // 一位数
                four: {
                  backgroundColor: "rgb(187,187,187)",
                  color: "white",
                  width: 12,
                  height: 16,
                  padding: [1, 0, 0, 5],
                  borderRadius: 10,
                  fontSize: 11,
                },
                // 两位数
                five: {
                  backgroundColor: "rgb(187,187,187)",
                  color: "white",
                  width: 16,
                  height: 16,
                  padding: [1, 0, 0, 1],
                  borderRadius: 10,
                  fontSize: 11,
                },
              },
            },
          },
          series: [{
            type: "bar",
            showBackground: true,
            label: {
              show: true,
              position: "right",
              color: "dodgerblue",
              fontWeight: 'bolder'
            },
            barWidth: 20,
            data: visitCounts,
            itemStyle: {
              //颜色样式部分
              borderRadius: 7,
              color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                {offset: 0, color: "royalblue"},
                {offset: 1, color: "#37BBF8"},
              ]),

            }
          },
          ],
        })
      }
    }
  },
  mounted() {
    this.load()
    this.search()
    //修改popper的所在位置
    this.$refs.here.appendChild(
        this.$refs.dropdown.popperElm
    )
  },
  computed: {
    //根据关键词类型显示按钮提示文字和图标
    btnWord() {
      switch (this.searchMode) {
        case 'visitor_id':
          return '根据到访用户搜索'
        case 'elder_id':
          return '根据访问老人搜索'
        case 'check_out_time':
          return '根据签到日期搜索'
        case 'visit_duration':
          return '根据访问时长搜索'
        default:
          return '搜索'
      }
    }
  },
}
</script>

